  },
  methods: {
    homeClick(){
        this.$router.push("/home")
    },
    addClass(t) {
      this.a = t;
    },
    carClick() {
      this.$router.push("/buycar");
    },
    shouyeClick() {
      this.$router.push("/home");
    },
    searchliClick(item) {
      this.searchVal = item.name;
      this.sub();
    },
    searchchange() {
      if (this.searchVal != "") {
        this.issearchul = true;
      } else {
        this.issearchul = false;
      }
    },
    sub() {
      for (let item of this.list) {
        if (item.name == this.searchVal) {
          if (item.scrollY) {
            window.scrollTo(0, item.scrollY);
          }
          return;
        }
      }
    },
    myorder() {
      this.$router.push("/myorder");
    },
    login() {
      this.$router.push("/login");
    },
    register() {
      this.$router.push("/register");
    }
  },
  computed: {
    totalPrice() {
      let price = 0;
      for (let item of this.$store.state.order) {
        price += item.count * parseInt(item.price);
      }
      return price;
    },
    list() {
      let arr = [];
      for (let item of this.goodsList) {
        if (item.name.search(this.searchVal) != -1) {
          arr.push(item);
        }
      }
      return arr;
    },
    items() {
      return this.$store.state.order;
    }
  }
